.projects__container {
    display: grid;
    grid-template-columns: 3fr 2fr;
    gap: 2.5rem;
    padding-bottom: 7rem;
}

.projects__container2 {
    display: grid;
    grid-template-columns: 2fr 3fr;
    gap: 2.5rem;
    padding-bottom: 7rem;
}


.projects__text{
    margin-top: 2rem;
}

