/* MEDIA QUERIES (MEDIUM DEVICES) */
@media screen and (max-width:1024px) {
    .projects__container {
        grid-template-columns: 1fr;
    }


    .projects__container > div {
        width: 80%;
        margin: 0 auto;
    }


}

/* MEDIA QUERIES (SMALL DEVICES) */
@media screen and (max-width:600px) {
    .projects__container {
        grid-template-columns: 1fr;
        gap: 1rem;
    }

    .projects__container > div{
        width: 100%;
        padding: 2rem 1rem;
    }


}